@import '../../styles/global.styles';

// ************* FORM INPUT COMPONENT STYLES ************* //
.formInput {
  padding: 0.5rem 1rem;
  
  // CLIENT INPUT (ClientCreate.js)
  &.clientInput {
    border: none;
    border-left: 1px solid lighten($dark-grey, 10%);
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
    font-size: 1.3rem;
    width: 100%;

    &.clientInput::placeholder {
      color: $dark-grey;
    }
  }
  
  // SIGN UP INPUT (SignupPage.js)
  &.signupInput {
    border: 1px solid lighten($dark-grey, 10%);
    border-radius: 0.5rem;
    font-size: 1.3rem;
    width: 100%;
  }

  // SIGN IN INPUT (SigninPage.js)
  &.signinInput {

  }
  
}

